import React from 'react'
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import './TDtail.css'


class TDtail extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      color: ''
    }
  }
  componentDidMount() {
    console.log(this.props);
    var ifExist = this.props.starlist.some(item => {
      if (item.detail == this.props.location.detail) {
        return true;
      }
    });
    console.log(ifExist)
    if (ifExist) {
      this.setState({
        color: 'orange'
      })
    }
  }

  render() {
    return (
      <div className='container'>
        <img src={this.props.location.src}></img>
        <p className="title">{this.props.location.detail}</p>

        <div className='go-back'
          onClick={() => this.props.history.push('/')}>＜</div>
        <div className='tab-bottom' style={{ position: 'fixed', bottom: 0 }}>
          <div className='choose-shop' className='tl' style={{ display: 'inline-block' }}>
            <div className='iconfont icon-dianpu' style={{ display: 'inline-block' }} />
            <div className='tab-text'>店铺</div>
          </div>
          <div className='choose-tel' className='tl' style={{ display: 'inline-block' }}>
            <div className='iconfont icon-fuwupingjia-kefu' style={{ display: 'inline-block' }} />
            <div className='tab-text'>客服</div>
          </div>
          <div className='choose-star' className='tl' style={{ display: 'inline-block' }} onClick={() => {
            // this.style.color = "yellow";
            console.log(this.props.location)
            if (!this.props.userinfo) {
              this.props.history.push(
                {
                  pathname: '/login',
                  src: this.props.location.src,
                  detail: this.props.location.detail,
                  last:this.props.location.pathname
                })
              return
            }
            this.props.dispatch({
              type: 'ADD',
              src: this.props.location.src,
              detail: this.props.location.detail
            })
          }}>
            <div className='iconfont icon-favorite' style={{ color: this.state.color, display: 'inline-block' }} onClick={(e) => this.getValue(e)} />
            <div className='tab-text'>收藏</div>
          </div>
          <div className='choose-two' style={{ display: 'inline-block', }}>
            <div className='cl' style={{ color: 'white', display: 'inline-block', background: '#FFC500' }}>加入购物车</div>
            <div className='cr' style={{ color: 'white', display: 'inline-block', background: '#FF9402' }}>立即购买</div>
          </div>
        </div>
      </div>
    )
  }
  getValue = (a) => {
    console.log(a.target)
    a.target.style.color = 'orange'
  }
}

// export default TDtail
const mapStateToProps = (state) => {
  console.log(state)
  return {
    starlist: state.starreducer,
    userinfo: state.loginreducer.userinfo
  }
}

export default connect(mapStateToProps)(withRouter(TDtail))