import React from 'react'
import { connect } from 'react-redux';
import './TLogin.css'


const TLogin = (props) => {
    console.log(props)
    return (
        <div className="container">
            <div className="img" style={{ display: "block", margin: "80px auto" }}></div>
            <input type="text" id="user" placeholder='手机号/邮箱/会员名' />
            <input type="text" id="pw" placeholder='请输入登录密码' />
            <button style={{ width: "80%", height: '40px', color: 'white', borderRadius: '20px', background: '-webkit-linear-gradient(left,#ff9000,#ff5000) no-repeat', border: '1px solid #ff9000', boxShadow: '0 2px 4px #f7c7b1' }}
                onClick={() => {
                    props.dispatch((dispatch) => {
                        let url = 'https://www.fastmock.site/mock/9e4bbf9e35ad15942010865690c87ac6/api/login';
                        fetch(url, {
                            method: 'post',
                            headers: {
                                'Content-Type': 'application/json'
                            },
                        })
                            .then(res => res.json())
                            .then(res => {
                                dispatch({
                                    type: 'LOGIN',
                                    userinfo: res.data.name
                                })
                            })
                    })
                    props.history.push({
                        pathname: props.location.last,
                        // pathname: '/detail',
                        src: props.location.src,
                        detail: props.location.detail
                    })
                    console.log(props.location)
                    // props.history.goBack();
                }}
            >登录</button>

        </div>
    )
}

const mapStateToProps = (state) => {
    console.log(state);
    return {
        username: state.username,
        last:state.last
    }
}

export default connect(mapStateToProps)(TLogin)
