import React from 'react'
import './THome.css'
import { Carousel, WingBlank } from 'antd-mobile';
import { connect } from 'react-redux'

//https://www.fastmock.site/mock/9e4bbf9e35ad15942010865690c87ac6/api/getgoodslist
//https://www.fastmock.site/mock/9e4bbf9e35ad15942010865690c87ac6/api/login


class THome extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            data: ['https://gw.alicdn.com/imgextra/i4/67/O1CN01yPXGic1CMjAP1OVdX_!!67-0-lubanu.jpg', 'https://aecpm.alicdn.com/simba/img/TB1CWf9KpXXXXbuXpXXSutbFXXX.jpg_q50.jpg', 'https://img.alicdn.com/tps/i4/https://img.alicdn.com/imgextra/i2/6000000001614/O1CN01cQKZF41NnFsbley0k_!!6000000001614-0-octopus.jpg', 'https://gw.alicdn.com/imgextra/i1/83/O1CN01bG7ayR1CU3VHv3GtK_!!83-0-lubanu.jpg', 'https://gw.alicdn.com/imgextra/i2/170/O1CN0104RBU51D7txX4Ebnc_!!170-0-lubanu.jpg'],
            imgHeight: 176,
        }
    }

    //请求数据
    componentDidMount() {
        this.props.dispatch((dispatch) => {
            let url = 'https://www.fastmock.site/mock/9e4bbf9e35ad15942010865690c87ac6/api/getgoodslist';
            fetch(url)
                .then(res => res.json())
                .then(res => {
                    dispatch({
                        type: 'LIST',
                        dataList: res.data
                    })
                });
        });
    }

    render(props) {
        console.log(this.props.goodslist)
        return (
            <div className='container'>
                <div className='top'>
                    <img src='https://gw.alicdn.com/bao/uploaded/TB1FjNySpXXXXc8aXXXXXXXXXXX-200-200.png' />
                    <input className='top-search' placeholder='寻找宝贝 店铺'
                        onClick={() => this.props.history.push({ pathname: '/search' })} />
                </div>

                <div className='context'>
                    {/* 轮播图 */}
                    <WingBlank className='carousel'>
                        <Carousel autoplay={true}>
                            {this.state.data.map(val => (
                                <a key={val}
                                    href="http://www.edu2act.cn"
                                    style={{ display: 'inline-block', height: this.state.imgHeight }}
                                >
                                    <img
                                        src={`${val}`}
                                        // style={{ width: '100%', verticalAlign: 'top' }}
                                        onLoad={() => {
                                            window.dispatchEvent(new Event('resize'));
                                            this.setState({ imgHeight: 'auto' });
                                        }}
                                    />
                                </a>
                            ))}
                        </Carousel>
                    </WingBlank>
                    {/* 商品列表 */}
                    <div className='A-list'>
                        {
                            this.props.datalist.map((item, i) => {
                                return <li key={item.id} onClick={() => {
                                    this.props.history.push(
                                        {
                                            pathname: '/detail',
                                            src: item.img,
                                            detail: item.title
                                        }
                                    )
                                }}>
                                    <img src={item.img}/>
                                    <p className='p-text'>{item.title}</p>
                                </li>
                            })
                        }
                    </div>
                </div>
            </div>
        )
    }
}

const mapStateToProps = (state) => {
    console.log(state);
    return {
        datalist: state.goodsreducer.datalist
    }
}

export default connect(mapStateToProps)(THome)
