import React from 'react'
import { connect } from 'react-redux'
import './TStar.css'
const TStar = (props) => {
    console.log(props)
    if(props.starlist.length === 0){
        return <div style={{width:'100%'}}>
                    <div style={{textAlign:'center',fontSize:'30px',color:'lightgrey',marginTop:'240px'}}>空空如也</div>
                    <div style={{textAlign:'center',fontSize:'30px',color:'lightgrey',marginTop:'30px'}}>╮(╯▽╰)╭</div>
                </div>
    }
    return (
        <div className='A-list'>
            {
                props.starlist.map((item, i) => {
                    return <li key={item.id} style={{width:'90%',height:'80px',position:'relative'}}>
                        <img src={item.src} style={{display:'inline-block',width:'auto',height:'100%'}} 
                        onClick={() => {
                            props.history.push(
                                {
                                    pathname: '/detail',
                                    src: item.src,
                                    detail: item.detail
                                }
                            )
                        }}/>
                        <p className='p-text' style={{display:'inline-block',position:'absolute',left:'30%',top:'auto'}}
                        onClick={() => {
                            props.history.push(
                                {
                                    pathname: '/detail',
                                    src: item.src,
                                    detail: item.detail
                                }
                            )
                        }}
                        >{item.detail}</p>
                        <div className="del" style={{width:'15%',height:'100%',textAlign:'center',lineHeight:'80px',position:'absolute',right:'0',display:'inline-block'}}
                        onClick={()=>{
                            props.dispatch({
                                type:'DELETE',
                                index:i
                            })
                        }}>删除</div>
                    </li>
                })
            }
        </div>
    )
}

const mapStateToProps = (state)=>{
    console.log(state);
    return {
        starlist:state.starreducer
    }
}

export default connect(mapStateToProps)(TStar)
