import React from 'react';
import {TabBar} from 'antd-mobile';
// import {Button} from 'antd-mobile';
import { withRouter } from 'react-router-dom';
import './Mytab.js'
import { connect } from 'react-redux';

class Mytab extends React.Component {
    constructor(props) {
      super(props);
      this.state = {
        selectedTab: '/home'
      };
    }

      render() {
        const {pathname} = this.props.location;
        console.log(pathname);
        if(pathname.split('/').length>2){
          return null;
        }
        if(pathname === '/detail' || pathname === '/login'){
          return null
        }
        return (
          <div style={{ 
              width: '100%', 
              position: 'fixed', 
              bottom: 0
            }}>
            <TabBar
              unselectedTintColor="#949494"
              tintColor="#F88400"
              barTintColor="white"
            >
              <TabBar.Item
                title="首页"
                key="home"
                icon={<div
                ><i className='iconfont icon-shouye'/></div>
                }
                selectedIcon={<div
                ><i className='iconfont icon-xuanzhongshangcheng'/></div>
                }
                selected={pathname === "/"}
                onPress={() => {
                  this.props.history.push('/');
                }}
                data-seed="logId"
              >
                {/* 首页 */}
              </TabBar.Item>

              <TabBar.Item
                icon={
                  <div
                ><i className='iconfont icon-gouwuche1'/></div>
                }
                selectedIcon={
                  <div
                ><i className='iconfont icon-gouwuche'/></div>
                }
                title="收藏"
                key="star"
                selected={pathname === "/star"}
                onPress={() => {
                  console.log(this)
                  if(this.props.userinfo==""){
                    this.props.history.push({
                      pathname:'/login',
                      last:'/star'
                    })
                    return
                  }
                  this.props.history.push('/star');
                }}
                data-seed="logId1"
              >
                {/* 购物车 */}
              </TabBar.Item>

              <TabBar.Item
                icon={
                  <div
                ><i className='iconfont icon-dd'/></div>
                }
                selectedIcon={
                  <div
                ><i className='iconfont icon-dingdan'/></div>
                }
                title="订单列表"
                key="list"
                selected={pathname === '/list'}
                onPress={() => {
                  this.props.history.push('/list');
                }}
              >
                {/* 我的订单 */}
              </TabBar.Item>

              <TabBar.Item
                icon={
                  <div
                ><i className='iconfont icon-wode'/></div>
                }
                selectedIcon={
                  <div
                ><i className='iconfont icon-wodedangxuan'/></div>
                }
                title="我的淘宝"
                key="my"
                selected={pathname === '/my'}
                onPress={() => {
                  this.props.history.push('/my');
                }}
              >
                {/* 社区页 */}
              </TabBar.Item>
            </TabBar>
          </div>
        );
      }
}
// export default withRouter( Mytab )

const mapStateToProps = (state) => {
  console.log(state)
  return {
    userinfo: state.loginreducer.userinfo
  }
}

export default connect(mapStateToProps)(withRouter(Mytab))
