
const goodsreducer=(state={datalist:[]},action)=>{
    if(action.type==='LIST'){
        console.log(action);
        return {datalist:action.dataList}
    }
    return state;
}
const loginreducer=(state={userinfo:''},action)=>{
    if(action.type==='LOGIN'){
        console.log(action);
        return {userinfo:action.userinfo}
    }
    return state;
}

let starlist=[];
function starreducer(state=starlist,action){
    if(action.type=='ADD'){
        console.log(action)
        return [...state,action]
    }
    if(action.type=='DELETE'){
        console.log(action.index)
        let list = [...state];
        list.splice(action.index,1);
        return list;
    }
    return state;
}


export {goodsreducer,starreducer,loginreducer}

